* {
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: lightgray;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

header {
  text-align: center;
}

ol {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

li {
  margin: 10px;
  text-align: center;
  background: #f1f1f1;
  width: 148px;
}

.pokemon-img {
  padding: 20px;
}

li p {
  background: gray;
  color: #fffff1;
  padding: 2px;
}

.search input {
  height: 22px;
  width: 200px;;
  border: none;
  margin: 12px;
  font-size: 14px;
  padding: 4px;
}

.filter-buttons button {
  width: 48px;
  height: 24px;
  border: none;
  margin: 4px;
}